          </div>
        </section>
      </div>
    </div>
    <div class="section1" v-if="tabInd == 1">
      <div class="tips">录入订单: 保存等待提交审核</div>
      <div class="item" v-for="(item, index) in itemList" :key="index">
        <div class="upload-img">
          <p style="font-size: 14px; padding-bottom: 10px">来料图片</p>
          <van-uploader
            v-model="fileList" 
            max-count="1"
            :after-read="afterRead"
            :preview-full-image="false"
            
          />
        </div>
        <van-cell-group>
          <van-dropdown-menu v-if="role > 0">
            <van-dropdown-item
              @change="changeStore(index)"
              v-model="value1"
              :options="option1"
            />
          </van-dropdown-menu>
          <van-field
