            label="产品名称"
            placeholder="请输入产品名称"
          />
          <van-field
            v-model="item.material"
            label="来料材质"
            placeholder="请输入来料材质"
          />
          <div class="flex-sa">
            <van-field
              v-model="item.number"
              label="来料数量"
              placeholder="值"
              type="number"
            />
            <van-field
              v-model="item.com_number"
              label="交付数量"
              placeholder="值"
              type="number"
            />
          </div>
          <div class="flex-sa">
            <van-field
              v-model="item.length"
              label="来料长(毫米)"
              placeholder="值 "
              type="number"
            />
            <van-field
              v-model="item.width"
              label="来料宽(毫米)"
              placeholder="值"
              type="number"
            />
          </div>
          <div class="flex-sa">
            <van-field
              v-model="item.inch_length"
              label="坑纸长(英寸)"
              placeholder="值 "
              type="number"
            />
            <van-field
              v-model="item.inch_width"
              label="坑纸宽(英寸)"
              placeholder="值"
              type="number"
            />
          </div>
          <van-field
            v-model="item.factory"
            label="印刷工厂"
            placeholder="请输入印刷工厂"
          />
          <van-cell
            title="交货日期"
            :value="item.com_time"
            @click="Clickshowtime(index)"
          />

          <van-calendar
            color="#40b9ec"
            v-model="showtime"
            placeholder="点击选择交货日期"
            @confirm="onConfirmtime"
          />

          <van-cell title="是否回厂">
            <template #right-icon>
              <van-radio-group v-model="item.is_return" direction="horizontal">
                <van-radio name="1">是</van-radio>
                <van-radio name="0">否</van-radio>
              </van-radio-group>
            </template>
          </van-cell>
          <van-cell
            v-if="item.is_return == 1"
            title="回厂时间"
            :value="item.return"
            @click="Clickreturntime(index)"
          />
          <van-calendar
            color="#40b9ec"
            v-model="returntime"
            placeholder="点击选择交货日期"
            @confirm="onrConfirmtime"
          />
          <van-field
            v-model="item.remark"
            rows="2"
            autosize
            label="备注"
            type="textarea"
            maxlength="50"
            placeholder="请输入备注"
            show-word-limit
          />

          <van-collapse v-model="activeNames">
            <block v-for="(sitem, sindex) in item.processList" :key="sindex">
              <van-collapse-item
                :title="sitem.pname"
                :name="sindex"
                v-if="sitem.is_yes == 0"
              >
                <van-radio-group v-model="sitem.next_level_id">
                  <van-field
                    v-model="items.text"
                    center
                    clearable
                    :label="items.pname"
                    placeholder="备注"
                    v-for="(items, indexs) in sitem.next_level"
                    :key="indexs"
                  >
                    <template #button>
                      <van-radio
                        :name="items.pid"
                        @click="onInput(items.checked, index, sindex, indexs)"
                      />
                    </template>
                  </van-field>
                </van-radio-group>
              </van-collapse-item>

              <van-field
                v-else
                v-model="sitem.text"
                center
                clearable
                :label="sitem.pname"
                placeholder="备注"
              >
                <template #button>
                  <van-switch
                    @input="onsInput(sitem.checked, index, sindex)"
                    :value="sitem.checked"
                    size="24"
                  />
                </template>
              </van-field>
            </block>
          </van-collapse>
        </van-cell-group>

        <div class="flex-sa">
          <div @click="savePro(index)" class="save-item">保存</div>
        </div>
      </div>

      <!-- <div class="add-btn">
        <van-button size="normal" @click="switchStatus" block round type="info"
          >添加来料工艺流程</van-button
        >
      </div> -->
    </div>
