<script>
import {
  addPorder,
  getPro,
  addPcart,
  getUsers,
  Fileuploader,
} from "@api/public";
import {
  Button,
  DropdownMenu,
  DropdownItem,
  Calendar,
  RadioGroup,
  Toast,
  Uploader,
  Switch,
  Cell,
  Radio,
  Collapse,
  Field,
  CollapseItem,
  Icon,
} from "vant";
import localStorage from "@utils/store/localStorage";
import { getPorder } from "@api/public";
import Loadings from "@components/Loadings";
export default {
  name: "Index",
  components: {
    [Calendar.name]: Calendar,
    [DropdownMenu.name]: DropdownMenu,
    [Toast.name]: Toast,
    [DropdownItem.name]: DropdownItem,
    [Button.name]: Button,
    [CollapseItem.name]: CollapseItem,
    [Cell.name]: Cell,
    [Uploader.name]: Uploader,
    [Collapse.name]: Collapse,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Switch.name]: Switch,
    [Field.name]: Field,
    [Icon.name]: Icon,
    Loadings,
  },
  props: {},
  data: function () {
    return {
      fileList: [],
      tabInd: 0,
      value1: 0,
      showtime: false,
      orderlist: [],
      image: "",
      returntime: false,
      option1: [{ text: "请选择客户", value: 0 }],
      itemList: [
        {
          factory: "",
          name: "",
          material: "",
          number: "",
          length: "",
          width: "",
          remark: "",
          user_name: "",
          com_number: "",
          inch_length: "",
          inch_width: "",
          is_return: "0",
          uid: "",
          com_time: "点击选择",
          return: "点击选择",
          id: "",
          image: "",
          processList: [],
          status: 1,
        },
      ],
      saveItemList: [],
      showPicker: false,
      processList: [],
      activeNames: [],
      show: true,
      pshow: true,
      jsonData: null,
      uid: 0,
      userName: "",
      userList: [],
      role: 0,
      editInd: 0,
      comTime: "",
      clickStatus: true,
    };
  },
  created() {
    var jsonData = JSON.parse(localStorage.get("userInfo"));
    this.jsonData = jsonData;

    getPro({})
      .then(({ data }) => {
        data.forEach((item) => {
          item.checked = false;
          item.text = "";
          item.next_level_id = "";
          item.next_level_ind = "-1";
          item.next_level.forEach((sitem) => {
            sitem.checked = false;
            sitem.text = "";
          });
        });

        this.processList = data;
        var itemList = this.itemList;
        this.itemList[0]["processList"] = data;
        this.itemList = itemList;
      })
      .catch((e) => {
        Toast(e.msg);
      });
    this.role = jsonData.role;
    if (jsonData.role == 1) {
      getUsers({ type: 0 })
        .then(({ data }) => {
          var option = [{ text: "点击选择客户名称", value: 0 }];
          this.userList = data;
          data.forEach((item, index) => {
            var obj = {};
            obj["text"] = item.signature ? item.signature : item.user_login;
            obj["value"] = index + 1;
            option.push(obj);
          });
          this.option1 = option;
        })
        .catch((e) => {
          Toast(e.msg);
        });
    } else if(jsonData.role == 0) {
      this.uid = jsonData.id;
      this.userName = jsonData.signature
        ? jsonData.signature
        : jsonData.user_login;

      var itemList = this.itemList;
      itemList[0]["status"] = 0;
      itemList[0]["uid"] = jsonData.id;
      itemList[0]["userName"] = jsonData.id;
      itemList[0]["user_name"] = jsonData.signature
        ? jsonData.signature
        : jsonData.user_login;
      this.itemList = itemList;
    }
  },
  mounted: function () {
    this.getData();
  },
  methods: {
    clickTab(e) {
      this.tabInd = e;
    },
    getData() {
      getPorder({ page: "1" }).then((res) => {
        this.orderlist = res.data.list;
      });
    },
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
      var formData = new FormData(); //构造一个 FormData，把后台需要发送的参数添加
      formData.append("file", file.file);
      console.log(file);
      console.log(formData);
      Fileuploader(formData).then(({ data }) => {
        this.$set(
          this.itemList[0],
          "image",
          data.themb ? data.themb : data.url
        );
      });
    },
    changeStore(e) {
      var itemList = this.itemList;
      itemList[e]["uid"] = this.userList[this.value1 - 1].id;
      itemList[e]["user_name"] = this.userList[this.value1 - 1].signature
        ? this.userList[this.value1 - 1].signature
        : this.userList[this.value1 - 1].user_login;
      this.itemList = itemList;
    },
    changeUserName(e) {
      var itemList = this.itemList;
      itemList[e]["userName"] = this.userName;
      itemList[e]["user_name"] = this.userName;
      this.itemList = itemList;
    },
    remove(e) {
      var itemList = this.itemList;
      itemList.splice(e, 1);
      this.itemList = itemList;
    },
    savePro(e) {
      var itemList = this.itemList[e];
      var processList = itemList.processList;
      var processListData = [];
      processList.forEach((item) => {
        var obj = {};
        if (item.is_yes == 1) {
          if (item.checked) {
            obj[item.pid] = item.text;
            processListData.push(obj);
          }
        } else {
          if (item.next_level_ind != "-1") {
            var obj = {};
            obj[item["next_level"][item.next_level_ind]["pid"]] =
              item["next_level"][item.next_level_ind]["text"];
            processListData.push(obj);
          }
        }
      });
      console.log(processListData);
      itemList["data"] = processListData;
      if (!this.clickStatus) {
      } else if (itemList["user_name"] == "") {
        Toast("请输入客户名称");
      } else if (itemList["name"] == "") {
        Toast("请输入产品名称");
      } else if (itemList["material"] == "") {
        Toast(" 请输入产品材质");
      } else if (itemList["number"] == "") {
        Toast(" 请输入产品数量");
      } else if (itemList["com_number"] == "") {
        Toast(" 请输入交付数量");
      } else if (itemList["width"] == "") {
        Toast("请输入产品宽度 ");
      } else if (itemList["length"] == "") {
        Toast("请输入产品长度 ");
      } else if (itemList["com_time"] == "点击选择") {
        Toast("请选择交货日期 ");
      } else if (
        itemList["is_return"] == "1" &&
        itemList["return"] == "点击选择"
      ) {
        Toast("请选择回厂日期 ");
      } else {
        this.clickStatus = false;
        addPorder(itemList)
          .then(({ data }) => {
            Toast("保存成功");
            location.reload();
          })
          .catch((e) => {
            Toast(e.msg);
            this.clickStatus = true;
          });
      }
    },
    onConfirmtime(date) {
      this.comTime = `${date.getFullYear()}-${
        date.getMonth() + 1
      }-${date.getDate()}`;
      this.showtime = false;
      var itemList = this.itemList;
      itemList[this.editInd]["com_time"] = this.comTime;

      this.itemList = itemList;
    },
    onrConfirmtime(date) {
      this.comTime = `${date.getFullYear()}-${
        date.getMonth() + 1
      }-${date.getDate()}`;
      this.returntime = false;
      var itemList = this.itemList;
      itemList[this.editInd]["return"] = this.comTime;

      this.itemList = itemList;
    },
    Clickshowtime(ind) {
      this.showtime = true;
      this.editInd = ind;
    },
    Clickreturntime(ind) {
      this.returntime = true;
      this.editInd = ind;
    },

    subPro() {
      console.log(this.saveItemList);
      var data = [];
      this.saveItemList.forEach((item) => {
        var obj = {};
        var arr = [];
        obj["oid"] = item.id;
        item.processList.forEach((sitem) => {
          if (sitem.checked) {
            arr.push(sitem.pid);
          }
        });
        obj["proid"] = arr;
        data.push(obj);
      });

      addPcart({ data })
        .then(({ data }) => {
          Toast("提交成功");
          location.reload();
        })
        .catch((e) => {
          Toast(e.msg);
        });
    },
    addItem() {
      if (this.itemList.length == this.saveItemList.length) {
        var list = this.itemList;
        list.push({
          factory: "",
          name: "",
          material: "",
          number: "",
          length: "",
          width: "",
          id: "",
          is_return: "0",
          com_number: "",
          user_name: this.userName,
          uid: this.uid,
          inch_length: "",
          inch_length: "",
          remark: "",
          com_time: "点击选择",
          return: "点击选择",
          processList: this.processList,
        });
        this.itemList = list;
      } else {
        Toast("请先完善好其他来料 再添加");
      }
    },
    switchStatus() {
      if (this.saveItemList.length > 0) {
        this.show = !this.show;
      } else {
        Toast("请先保存好添加完成的来料");
      }
    },
    onInput(e, i, inds, ind) {
      var itemList = this.itemList;
      console.log(666);
      itemList[i]["processList"][inds]["next_level_ind"] = ind;
      this.itemList = itemList;
      console.log(itemList);
    },
    onsInput(e, i, inds) {
      console.log(e);
      console.log(i);
      console.log(inds);
      var itemList = this.itemList;

      itemList[i]["processList"][inds]["checked"] = !e;
      this.itemList = itemList;
      console.log(itemList);
    },
    formatDate: function (value) {
      let date = new Date(value * 1000);

      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d + " ";
    },
  },
};
</script>
