<template>
  <div>
    <header class="flex-sb">
      <div class="menu" @click="showLeftPopup">
        <van-icon name="bar-chart-o" size="20" />
      </div>
      <div class="flex-fs">
        <span class="home">
          <div @click="hideLeftDrawStatus">
            <van-icon name="wap-home-o" size="20" />
          </div>
        </span>
        <span @click="logout">退出</span>
      </div>
    </header>
  </div>
</template>

<style scoped>
.home {
  padding: 0 10px;
  margin-right: 10px;
  border-right: 1px solid #eee;
}
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 0 10px #eee;
  background: #fff;
  height: 50px;
  z-index: 16666;
  padding: 0 10px;
  font-size: 14px;
}
.menu {
  transform: rotate(90deg);
}
</style>

<script>
import { Icon } from "vant";
import localStorage from "@utils/store/localStorage";
export default {
  name: "Menu",
  props: {},
  components: {
    [Icon.name]: Icon,
  },
  data: function () {
    return {};
  },
  created() {},
  methods: {
    logout() {
      localStorage.remove("ntoken");
      this.$store.commit("hideLeftDrawStatus");
      this.$router.replace({ path: "/" });
    },
    hideLeftDrawStatus() {
      this.$store.commit("hideLeftDrawStatus");
      this.$router.replace({ path: "/index" });
    },
 
    showLeftPopup() {
      
      this.$store.commit("changeLeftDrawStatus"); 

    },
  },
};
</script>