<template>
  <div>
    <van-popup
      v-model="$store.state.leftDrawStatus" 
      position="left"
      style="width: 50%; padding-top: 50px; height: 100%; z-index: 10000"
    >
      <div class="info">
        <p><img v-if="avatar != ''" :src="avatar" alt="" /></p>
        <p class="name">{{ user_login }}</p>
      </div>
      <van-cell border="false" title="系统公告" is-link icon="volume-o" />
      <van-cell border="false" title="在线排单" is-link icon="cluster-o" />
      

      <van-collapse v-model="activeNames">
        <van-collapse-item title="生成报表" name="2" icon="chart-trending-o">
          <van-cell 
            title="产量报表"
            icon="orders-o"
            @click="hideLeftDrawStatus('/analysis')"
          />
          <van-cell
            title="生产进度表"
            icon="orders-o"
            @click="hideLeftDrawStatus('/dataCenter')"
            to="/dataCenter"
          />
        </van-collapse-item>
      </van-collapse>
      <van-cell border="false" title="品质异常" is-link icon="warn-o" />
      <van-cell border="false" title="人员绩效" is-link icon="friends-o" />
      <van-collapse v-model="activeNames">
        <van-collapse-item title="个人中心" name="1" icon="user-o">
          <van-cell
            title="密码修改"
            icon="eye-o"
            @click="hideLeftDrawStatus('/change_password')"
            to="/change_password"
          />
          <van-cell
            title="资料修改"
            icon="records"
            @click="hideLeftDrawStatus('/changeInfo')"
            to="/changeInfo"
          />
        </van-collapse-item>
      </van-collapse>
      <van-collapse v-model="activeNames">
        <van-collapse-item title="退出登录" name="3" icon="exchange">
          <van-cell title="安全退出" icon="exchange" @click="logout" />
        </van-collapse-item>
      </van-collapse>
    </van-popup>
  </div>
</template>

<style scoped>
.info {
  color: #fff;
  background: #27363d;
  padding: 15px 10px 20px;
  font-size: 14px;

  text-align: center;
}
.info img {
  width: 65px;
  border-radius: 50%;
  margin: 10px 0;
}
</style>

<script>
import { Popup, Cell, Collapse, CollapseItem } from "vant";
import localStorage from "@utils/store/localStorage";

export default {
  name: "Sizebar",
  props: {},
  components: {
    [Collapse.name]: Collapse,
    [CollapseItem.name]: CollapseItem,
    [Cell.name]: Cell,
    [Popup.name]: Popup,
  },
  data: function () {
    return {
      activeNames: [],
      user_login: "",
      avatar: "",
    };
  },
  created() {
    var jsonData = JSON.parse(localStorage.get("userInfo"));
    this.user_login = jsonData.user_login;
    this.avatar = jsonData.avatar;
  },
  methods: {
    hideLeftDrawStatus(e) {
      this.$store.commit("hideLeftDrawStatus");

      this.$router.replace({ path: e });
    },

    logout() {
      localStorage.remove("ntoken");
      this.$store.commit("hideLeftDrawStatus");
      this.$router.replace({ path: "/" });
    },
  },
};
</script>